import React, { useState } from 'react';
import {useSelector} from 'react-redux';
import {Link} from 'react-router-dom';

import Container from '../container/Container';
import logo from '../../assets/images/venia-logo.PNG';
import shoppingBag from '../../assets/images/shopping-bag-white.svg';
import menuIcon from '../../assets/images/menu.svg';
import MainContainer from '../container/MainContainer';

export default function Header() {
    const store = useSelector(state => state.data);
    const [selectedId, setSelectedId] = useState('1');

    let totalProductsQuantity = 0;
    store.productData.map((cartItem) => {
        totalProductsQuantity += cartItem.productsCount;
    });

    const makeActive = (event) => {
        setSelectedId(event.target.id)
        console.log(event.target.id)
    }
    

  return (
    <MainContainer>
        <header className='header-wrapper'>
            <Container>
                <div className='header'>
                    <a href='#' id='menuButton'>
                        <img src={menuIcon} alt="menu icon"/>
                    </a>
                    <div className='logo'>
                        <Link to='/'>
                            <img src={logo} id='venieLogo' alt='venia logo' />
                        </Link>
                    </div>
                    <div className='header-menu-1'>
                        <ul>
                            <li id='1' className={selectedId === '1' ? 'active' : ''} onClick={(event) => makeActive(event)}>Home</li>
                            <li id='2' className={selectedId === '2' ? 'active' : ''} onClick={(event) => makeActive(event)}>Women</li>
                            <li id='3' className={selectedId === '3' ? 'active' : ''} onClick={(event) => makeActive(event)}>Men</li>
                            <li id='4' className={selectedId === '4' ? 'active' : ''} onClick={(event) => makeActive(event)}>Smart Gear</li>
                            <li id='5' className={selectedId === '5' ? 'active' : ''} onClick={(event) => makeActive(event)}>Accessories</li>
                        </ul>
                    </div>
                    <div className='header-menu-2'>
                        <Link to='/cart' className='header-menu-2-link'>
                            <img src={shoppingBag} id='shoppingBag' alt='cart icon'/>
                            <span className='cart-item-count-display'>
                                {totalProductsQuantity}
                            </span>
                        </Link>
                    </div>
                </div>
            </Container>
        </header>
    </MainContainer>
  )
}
