import React, { Fragment, useEffect } from 'react';
import {useSelector, useDispatch} from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {loadProducts} from '../../redux/action';

import MainContainer from '../container/MainContainer';
import Container from '../container/Container';
import threeMen from '../../assets/images/three-men-clothing.png';
import manInShade from '../../assets/images/men-in-shade.png';
import aloneMan from '../../assets/images/alone-man.jpg';

export default function Home() {
    const dispatch = useDispatch();
    const store = useSelector(state => state.data);
    const navigate = useNavigate();

    let categoryImages = {
        Women: '',
        Men: '',
        Jwellery: '',
        Electronics: ''
    }

    store.products.map(item => {
        if(item.category === "women's clothing")
            categoryImages.Women = item.image
        if(item.category === "men's clothing")
            categoryImages.Men = item.image
        if(item.category === "jewelery")
            categoryImages.Jwellery = item.image
        if(item.category === "electronics")
            categoryImages.Electronics = item.image
    })

    console.log(categoryImages);

    const navigateToProductList = (event) => {
        event.preventDefault();
        navigate('/product-list');
    }

    //load products from redux store
    useEffect(() => {
        dispatch(loadProducts());
    }, []);
    
  return (
    <MainContainer>
        <div className='home'>
            <div className='hero-banner-1'>
                <div className='hero-banner-info'>
                    <h1>Shop the new Signature Collection</h1>
                    <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
                        tempor incididunt ut labore et dolore magna aliqua. Lobortis mattis 
                        aliquam faucibus purus.
                    </p>
                    <button type='button' onClick={navigateToProductList}>SHOW NOW</button>
                </div>
                <div className='hero-banner-image'>
                </div>
            </div>
            <Container>
                <div className='category-display-wrapper' onClick={navigateToProductList}>
                    <div className='category-display-box box-1'>
                        <div className='category-display-info'>
                            <h2>Shop Women</h2>
                            <p>Lorem ipsum dolor sit amet</p>
                        </div>
                    </div>
                    <div className='category-display-box box-2'>
                        <div className='category-display-info'>
                            <h2>Shop Men</h2>
                            <p>Lorem ipsum dolor sit amet</p>
                        </div>
                    </div>
                    <div className='category-display-box box-3'>
                        <div className='category-display-info'>
                            <h2>Jewellery</h2>
                            <p>Lorem ipsum dolor sit amet</p>
                        </div>
                    </div>
                    <div className='category-display-box box-4'>
                        <div className='category-display-info'>
                            <h2>Electronics</h2>
                            <p>Lorem ipsum dolor sit amet</p>
                        </div>
                    </div>
                </div>
                <div className='hero-banner-2'>
                    <div className='hero-banner-2-text'>
                        <h1>Take off in the new Signature Legging</h1>
                        <h2>Lorem Ipsum Dolor Tempor</h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, 
                            sed do eiusmod tempor labore dolore magna lorem ipsum dolor 
                            sit dolore magna.
                        </p>
                        <div className='hero-banner-2-buttons'>
                            <button type='button' onClick={navigateToProductList}>SHOP COLLECTION</button>
                            <button type='button' onClick={navigateToProductList}>SHOP NOW</button>
                        </div>
                        <div className='line'></div>
                    </div>
                    <div className='hero-banner-1-image'>

                    </div>
                </div>
                <div className='hero-banner-3'>
                    <div className='hero-banner-3-image'>
                        <img src={aloneMan} alt='alone man' />
                    </div>

                    <div className='hero-banner-3-info'>
                        <h1>Conquer your next adventure</h1>
                        <p>Lorem Ipsum Dolor Tempor</p>
                        <button type='button' onClick={navigateToProductList}>SHOP DEVICES</button>
                    </div>
            </div>
            </Container>
        </div>
    </MainContainer>
  )
}
